@import url(https://fonts.googleapis.com/css2?family=VT323&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Share+Tech+Mono&display=swap);




body {
  font-family: 'Share Tech Mono',
    monospace;
  font-weight: 100;
  margin: 0;
}

h1,
h2,
h3 {
  font-family: "VT323", sans-serif;
}


p {
  font-family: 'Share Tech Mono',
    monospace;
  font-weight: 400;
}

.toast {
  position: fixed;
  bottom: 10px;
  right: 10px;
  z-index: 99999;
  width: 350px;
}

button {
  background: none;
  border: none;
  padding: 0;
  margin: 0;
}

.myButton {
  width: 197px;
  background-color: #CB295F;
  color: #FFFFFF;
  font-family: "VT323",
    sans-serif;
  font-size: 18px;
  letter-spacing: 1.5px;
  line-height: 18px;
  text-align: center;
  padding: 10px 30px;
  display: block;
}

.myMiniButton {
  color: #1292B9;
  font-family: "VT323",
    sans-serif;
  font-size: 18px;
  letter-spacing: 1.5px;
  line-height: 18px;
  padding: 10px 30px;
  display: block;
}

.myButtonBlue {
  background-color: #1292B9;
  width: 197px;
  color: #FFFFFF;
  font-family: "VT323",
    sans-serif;
  font-size: 18px;
  letter-spacing: 1.5px;
  line-height: 18px;
  text-align: center;
  padding: 10px 30px;
  display: block;
}

.form-buttons {
  margin-top: 50px;
  display: flex;
  flex-direction: column;
  align-items: center;
}


img {
  display: block;
  width: 100%;
}

.error-message {
  background: red;
  color: white;
}

.input {
  background-color: #F5F6F9;
  border: none;
  border-bottom: 3px solid #1292B9;
}

.modal-backdrop {
  background-color: rgba(18, 146, 185, 0.5);
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px)
}

.myModal .modal-content {
  padding: 24px;
}

